import service from '@/plugin/axios/service'

export function getAllUser(page,size){
    return service({
        url:'/api/user/page',
        params:{page:page,size:size}
    })
}

export function updateUser(params){
    return service({
        url:'/api/user/',
        method:'put',
        data:params
    })
}

export function createUser(params){
    return service({
        url:'/api/user/',
        method:'post',
        data:params
    })
}

export function deleteUser(id){
    return service({
        url:'/api/user/'+id,
        method:'delete'
    })
}

export function getAllRoles(){
    return service({
        url:'/api/user/roles'
    })
}

export function searchUserByName(name,page,size){
    return service({
        url:'/api/user/search',
        params:{name:name,page:page,size:size}
    })
}

export function changePassword(origin,pass){
    return service({
        url:'/api/user/pass',
        method:'post',
        params:{origin:origin,pass:pass}
    })
}