<template>
  <div class="main">
    <div class="wrapper">
      <h2>用户管理</h2>
      <div class="button-wrapper"></div>
      <el-card class="box-card">
          <el-input
            v-model="searchText"
            placeholder="搜索用户"
            @input="searchUser(searchText,1,pageSize)"
            clearable
            class="search-box"
          ></el-input>
 
        <el-button style="float: right" @click="createNewUser">
          <i class="el-icon-plus"></i>添加
        </el-button>
        <div class="table-wrap">
          <normal-table
            :tableData="tableData"
            :columns="columns"
            :created="newUser"
            :changed="userEdit"
            :deleteFlag="deleteFlag"
            :index="true"
            :width="200"
            @handleEdit="editUser"
            @handleDelete="handleDelete"
          >
            <template #roles="{scope}">{{ getRoleName(scope.row.roles) }}</template>
            <template #clazz="{scope}">{{ getClassName(scope.row.clazz) }}</template>
          </normal-table>
        </div>
        <div class="block">
          <el-pagination
            layout="prev,pager,next"
            @current-change="currentChange"
            :current-page="currentPage"
            :total="total"
            :page-size="pageSize"
            background
          ></el-pagination>
        </div>
      </el-card>
      <el-dialog :visible.sync="dialogFormShow" :title="dialogStatus[flag]" center>
        <el-form ref="dialog" :model="dialog" :rules="checkRules()" size="mini">
          <el-form-item label="用户名" prop="name">
            <el-input v-model="dialog.name" maxlength="10"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="dialog.password" type="password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input v-model="dialog.checkPass" type="password"></el-input>
          </el-form-item>
          <el-form-item label="权限" prop="roles">
            <el-select v-model="dialog.roles" multiple value-key="id" placeholder="请选择">
              <el-option v-for="role in roles" :key="role.id" :label="role.role_name" :value="role"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="班级" prop="clazz">
            <el-select v-model="dialog.clazz" placeholder="请选择">
              <el-option
                v-for="clazz in classes"
                :key="clazz.id"
                :label="clazz.name"
                :value="clazz.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="flag==='edit'? handleUpdate():handleCreate()">提交</el-button>
          <el-button type="primary" @click="dialogFormShow=false">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getAllUser,
  createUser,
  updateUser,
  deleteUser,
  getAllRoles,
  searchUserByName
} from "@/api/user";
//import { getAllGrade } from "@/api/grade";
import store from "@/store/index";
import normalTable from "@/components/normalTable";

export default {
  name: "userManage",
  components: {
    normalTable
  },
  data() {
    var validatePass = (rule, value, callback) => {
      let pattern = /^[\w_-]{6,16}$/;
      if (!pattern.test(value)) {
        callback(new Error("密码不符合"));
      } else {
        if (this.dialog.checkPass !== "") {
          this.$refs.dialog.validateField("checkPass");
        }
        callback();
      }
    };

    var validateName = (rule, value, callback) => {
      // let patten = /^(?!_)(?!.*?_$)\w{5,10}$/
      let patten = /^[a-zA-Z0-9]+$/;
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        if (!patten.test(value)) {
          callback(new Error("用户名不能包括字符"));
        } else if (value.length < 5) {
          callback(new Error("用户名长度应大于5"));
        } else {
          callback();
        }
      }
    };

    var validateEdit = (rule, value, callback) => {
      if (value && value.length < 8) {
        callback(new Error("密码不符合"));
      } else {
        if (this.dialog.checkPass !== "") {
          this.$refs.dialog.validateField("checkPass");
        }
        callback();
      }
    };

    var validatePass2 = (rule, value, callback) => {
      if (this.flag === "create") {
        if (value !== null && value === "") {
          callback(new Error("请再次输入密码"));
        } else if (value !== this.dialog.password) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      } else {
        if (value !== this.dialog.password) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      }
    };

    return {
      tableData: [],
      currentPage: 1,
      pageSize: 5,
      total: undefined,

      searchText: "",
      searchFlag: false,

      roles: [],
      role_id: undefined,

      classes: [{ id: -2, name: "无" }, ...store.getters.classes],

      dialog: {
        id: undefined,
        name: "",
        roles: [],
        clazz: undefined,
        password: "",
        checkPass: ""
      },

      dialogFormShow: false,
      dialogStatus: {
        create: "添加新用户",
        edit: "修改用户信息"
      },

      flag: "",
      btn_stu: false,

      // table components
      newUser: null,
      userEdit: null,
      deleteFlag: false,

      editRule: {
        name: [{ validator: validateName, trigger: "change" }],
        password: [{ validator: validateEdit, trigger: "change" }],
        checkPass: [{ validator: validatePass2, trigger: "change" }],
        roles: [{ required: true, message: "请选择角色", trigger: "change" }],
        clazz: [{ required: true, message: "请选择班级", trigger: "change" }]
      },
      createRule: {
        name: [{ validator: validateName, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        roles: [{ required: true, message: "请选择角色", trigger: "change" }],
        clazz: [{ required: true, message: "请选择班级", trigger: "change" }]
      },
      columns: [
        {
          id: "name",
          label: "用户名"
        },
        {
          id: "roles",
          label: "权限"
        },
        {
          id: "clazz",
          label: "班级"
        }
      ]
    };
  },
  created() {
    this.initUser();
    this.initRoles();
    //this.getAllClasses();
  },

  computed: {
    name: function() {
      return store.getters.name;
    }
  },

  watch: {
    searchText: function() {
      if (this.searchText === "") {
        this.searchFlag = false;
      }
    }
  },

  methods: {
    initUser() {
      getAllUser(this.currentPage, this.pageSize).then(resp => {
        this.tableData = resp.data.data;
        this.total = resp.data.total;
      });
    },

    initRoles() {
      getAllRoles().then(resp => {
        this.roles = [...resp.data];
      });
    },

    checkRules() {
      if (this.flag === "edit") {
        return this.editRule;
      } else {
        return this.createRule;
      }
    },

    getClassName(clazzId) {
      for (var i in this.classes) {
        if (this.classes[i].id == clazzId) {
          return this.classes[i].name;
        }
      }
    },

    getRoleName(roles) {
      return roles.map(role => role.role_name);
    },

    currentChange(page) {
      this.currentPage = page;
      if (this.searchFlag) {
        this.searchUser(this.searchText, this.currentPage, this.pageSize);
      }
      this.initUser();
    },

    resetDialog() {
      this.dialog = {
        id: undefined,
        name: "",
        roles: [],
        clazz: undefined,
        password: "",
        checkPass: ""
      };
    },

    createNewUser() {
      this.dialogFormShow = true;
      this.flag = "create";
      this.resetDialog();
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    handleCreate() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          createUser(this.dialog)
            .then(resp => {
              if (resp.status === 200) {
                this.dialogFormShow = false;
                this.total += 1;
                this.dialog.id = resp.data.obj;
                this.newUser = this.dialog;
                this.$message({
                  type: "success",
                  message: "添加成功"
                });
              }
            })
            .catch(() => {
              this.$message.error("添加失败");
            });
        } else {
          return false;
        }
      });
    },

    editUser(row) {
      this.dialogFormShow = true;
      this.flag = "edit";
      // this.$nextTick(() => {
      //   this.$refs["dialog"].resetFields();
      // });
      row = { ...row, password: "", checkPass: "" };
      this.dialog = Object.assign({}, row);
      this.dialog.clazz = row.clazz;
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    handleUpdate() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          const dialogData = Object.assign({}, this.dialog);
          if (dialogData.password === "") {
            dialogData.password = null;
          }
          updateUser(dialogData)
            .then(() => {
              this.userEdit = dialogData;
              this.dialogFormShow = false;
              this.$message({
                message: "修改成功",
                type: "success"
              });
            })
            .catch(() => {
              this.$message({
                message: "修改失败",
                type: "error"
              });
            });
        } else {
          return false;
        }
      });
    },

    handleDelete(id) {
      deleteUser(id)
        .then(response => {
          if (response.status === 200) {
            this.deleteFlag = true;
            this.$message({
              type: "success",
              message: "删除成功"
            });
            this.total -= 1;
            if (this.total % this.pageSize == 0) {
              this.currentPage = this.currentPage - 1;
              this.initUser();
            }
          }
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: "删除失败"
          });
        });
    },

    searchUser(name, page, size) {
      this.searchFlag = true;
      if (page === 1) {
        this.currentPage = 1;
      }
      searchUserByName(name, page, size).then(response => {
        this.tableData = response.data.obj.data;
        this.total = response.data.obj.total;
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.table-wrap {
  padding: 10px;
}
@media (min-width: 320px) and (max-width: 768px) {
  .search-box {
    width: 200px;
  }
}

@media (min-width: 743px) {
  .search-box {
    width: 250px;
  }
}
</style>